export const spiralHowl = {
    cooldown: {
        shootingCur: 0,
        shootingMax: 8,
        burstCur: 0,
        burstMax: 10,
        burstTimeCur: 0,
        BurstTimeMax: 250,
    },
    pattern: ({ x, y, cooldown, size, bulletArr }) => {
        let j = cooldown.burstCur;
        const speed = 1;
        let antall = 12;
        let start = 0;
        let step = (2 * Math.PI) / antall;
        for (let i = 1; i <= antall; i++) {
            let angle = start + (j + i) * step;
            let vx = Math.sin(angle + 3 * j) * speed;
            let vy = Math.cos(angle + 3 * j) * speed;
            bulletArr.push(new BulletSpiral(x, y, size, size, 'green', [vx, vy], [0, 0]));
        }
    },
};
