export const spread5 = {
    cooldown: {
        shootingCur: 0,
        shootingMax: 8,
        burstCur: 0,
        burstMax: 10,
        burstTimeCur: 0,
        BurstTimeMax: 150,
    },
    pattern: ({ x, y, size, bulletArr }) => {
        let speed = 6;
        let amount = 5 - 2;
        let start = Math.PI;
        let spread = Math.PI / 24;
        for (let i = 0; i < amount; i++) {
            let angle = start + i * spread;
            let vx = Math.cos(angle) * speed;
            let vy = Math.sin(angle) * speed;
            bulletArr.push(new Bullet(x, y, size, size, 'green', [vx, vy], [0, 0]));
        }
        for (let i = 1; i <= amount - 1; i++) {
            let angle = start - i * spread;
            let vx = Math.cos(angle) * speed;
            let vy = Math.sin(angle) * speed;
            bulletArr.push(new Bullet(x, y, size, size, 'green', [vx, vy], [0, 0]));
        }
    },
};
