import Animal from './animal';
export default class Enemy extends Animal {
    outOfRange = false;
    bullets;
    player;
    hp = 0;
    type = 0;
    dead = false;
    shooting = false;
    target;
    pattern;
    cooldown = {
        shootingCur: 0,
        shootingMax: 8,
        burstCur: 0,
        burstMax: 10,
        burstTimeCur: 0,
        BurstTimeMax: 100,
    };
    bulletType;
    speed = 0;
    arrived = false;
    behaviourLogic;
    oldX = 0;
    oldY = 0;
    reverse = false;
    sprite = new Image();
    animation = {
        curFrame: 0,
        frames: 5,
        frameSpeed: 1,
        frameCurTimer: 0,
        frameDuration: 5,
    };
    constructor(x, y, width, height, color, bullets, player, bulletType, behaviourLogic, hp, reverse = false) {
        super(x, y, width, height, color);
        this.bullets = bullets;
        this.player = player;
        this.sprite.src = '/images/neko_sprite.png';
        this.behaviourLogic = behaviourLogic;
        this.bulletType = bulletType;
        this.updateBulletType();
        this.hp = hp;
        this.oldX = this.x;
        this.oldY = this.y;
        this.reverse = reverse;
    }
    updateBulletType() {
        const { cooldown, pattern } = this.bulletType ||
            this.behaviourLogic?.bulletTypes[this.behaviourLogic?.behaviour[this.behaviourLogic?.state]?.bulletType];
        this.cooldown = cooldown;
        this.pattern = pattern;
        this.target = [this.player.x, this.player.y];
        if (!this.behaviourLogic?.behaviour[this.behaviourLogic?.state]
            ?.shootAfterPathing)
            this.cooldown.burstTimeCur = this.cooldown.BurstTimeMax;
    }
    logic(ctx, dt) {
        const behaviour = this.behaviourLogic?.behaviour[this.behaviourLogic?.state];
        if (behaviour) {
            const x = behaviour.path.x || 0;
            const y = behaviour.path.y || 0;
            this.x = behaviour.easing.x(Math.min(this.behaviourLogic.stateDurationCur, behaviour.duration), this.oldX, x, behaviour.duration);
            this.y = behaviour.easing.y(Math.min(this.behaviourLogic.stateDurationCur, behaviour.duration), this.oldY, y, behaviour.duration);
            if (this.behaviourLogic.stateDurationCur < behaviour.duration)
                this.behaviourLogic.stateDurationCur += 0.01 * dt;
            if (!behaviour.shootAfterPathing &&
                this.behaviourLogic.stateDurationCur >= behaviour.shootAfter)
                this.shootingLogic(dt);
            if (this.behaviourLogic.stateDurationCur > behaviour.duration) {
                if (!this.arrived) {
                    this.arrived = true;
                    this.speed = 0;
                    this.cooldown.burstTimeCur = this.cooldown.BurstTimeMax;
                }
                if (behaviour.shootAfterPathing) {
                    if (this.behaviourLogic.stateDurationCur - behaviour.duration >=
                        behaviour.shootAfter)
                        this.shootingLogic(dt);
                    else
                        this.behaviourLogic.stateDurationCur += 0.01 * dt;
                }
                else
                    this.updateState();
            }
        }
        else {
            this.shootingLogic(dt);
        }
        this.animate();
        this.draw(ctx);
    }
    animate() {
        if (this.animation.frameCurTimer >= this.animation.frameDuration) {
            this.animation.frameCurTimer = 0;
            this.animation.curFrame++;
        }
        else
            this.animation.frameCurTimer += this.animation.frameSpeed;
        if (this.animation.curFrame === this.animation.frames)
            this.animation.curFrame = 0;
    }
    draw(ctx) {
        ctx.beginPath();
        ctx.fillStyle = this.color;
        ctx.rect(this.x, this.y, this.width, this.height);
        ctx.fill();
        if (this.sprite.complete)
            ctx.drawImage(this.sprite, 0, 245 * this.animation.curFrame, 430, 245, this.x, this.y, this.width, this.height);
        if (!this.outOfRange &&
            (this.x < -200 || this.x > 1200 || this.y < -200 || this.y > 600))
            this.outOfRange = true;
    }
    hit(dmg) {
        this.hp -= dmg || 1;
        if (this.hp <= 0) {
            this.dead = true;
        }
    }
    shootingLogic(dt) {
        if (this.shooting)
            this.shoot(dt);
        else {
            if (this.cooldown.burstTimeCur >= this.cooldown.BurstTimeMax) {
                this.cooldown.burstTimeCur = 0;
                this.shooting = true;
                this.target = [this.player.x, this.player.y];
            }
            else
                this.cooldown.burstTimeCur += 1 * dt;
        }
    }
    shoot(dt) {
        if (this.cooldown.shootingCur >= this.cooldown.shootingMax) {
            this.pattern({
                x: this.reverse ? this.x + this.width : this.x,
                y: this.y + this.height / 2 + 4,
                size: 8,
                cooldown: this.cooldown,
                bulletArr: this.bullets,
                player: this.player,
                target: this.target,
            });
            this.cooldown.shootingCur = 0;
            this.cooldown.burstCur += 1 * dt;
            if (this.cooldown.burstCur >= this.cooldown.burstMax * dt) {
                this.cooldown.burstCur = 0;
                this.shooting = false;
                const behaviour = this.behaviourLogic?.behaviour[this.behaviourLogic?.state];
                if (behaviour)
                    if (behaviour.shootAfterPathing) {
                        this.updateState();
                    }
            }
        }
        else {
            this.cooldown.shootingCur += 1 * dt;
        }
    }
    updateState() {
        this.behaviourLogic.state =
            (this.behaviourLogic.state + 1) % this.behaviourLogic.behaviour.length;
        this.behaviourLogic.stateDurationCur = 0;
        this.arrived = false;
        this.updateBulletType();
        this.oldX = this.x;
        this.oldY = this.y;
    }
}
