import Enemy from './enemy';
import { lerp } from './utils';
import { easeOutQuad } from './lib/easing';
export default class Boss extends Enemy {
    type = 1;
    maxHp = this.hp;
    hpPercent = 380;
    initialized = false;
    initializeTimer = {
        cur: 0,
        max: 20,
        startX: this.x,
    };
    logic(ctx, dt) {
        if (this.initialized) {
            super.logic(ctx, dt);
            this.drawBossHp(ctx);
        }
        else if (this.initializeTimer.cur >= this.initializeTimer.max) {
            this.initializeTimer.cur = 0;
            this.initialized = true;
        }
        else if (!this.initialized) {
            this.x = easeOutQuad(Math.min(this.initializeTimer.cur, this.initializeTimer.max), this.initializeTimer.startX, -360, this.initializeTimer.max);
            this.initializeTimer.cur += 0.1 * dt;
            super.draw(ctx);
        }
    }
    drawBossHp(ctx) {
        ctx.beginPath();
        ctx.fillStyle = this.color;
        ctx.rect(530, 470, 380, 40);
        ctx.fill();
        this.hpPercent = lerp(this.hpPercent, (380 * this.hp) / this.maxHp, 0.1);
        ctx.beginPath();
        ctx.fillStyle = 'rgba(200,0,0,0.7)';
        ctx.rect(530, 470, this.hpPercent, 40);
        ctx.fill();
        if (this.sprite.complete)
            ctx.drawImage(this.sprite, 0, 0, 430, 245, 500, 455, 103, 58);
    }
    hit(dmg) {
        if (this.initialized)
            super.hit(dmg);
    }
}
